.heading {
	font-weight: bold;
	font-size: 32px;
	line-height: 37px;
	color: $grey-3;
	padding-bottom: 30px; 
	margin: 0;
}
@media (max-width: 767px) {
	.heading {
		display: flex;
		justify-content: center;
		padding-bottom: 24px;
		&.heading-mob {
			font-size: 22px;
			line-height: 26px;
			padding-bottom: 30px;
		}
	}
}