.mob-menu{
    display: none;
    margin: 0 auto;
    font-size: 18px;
    line-height: 140%;
    width: 100%;
    & button {
        font-family: 'Roboto', 'Tahoma', sans-serif;
        cursor: pointer;
        position: relative;
        margin: 14.5px 10px;
        font-weight: bold;
        font-size: 18px;
        line-height: 110%;
        color: $grey-2;
        padding-right: 135px;
        border: none;
        background: transparent;
        &:hover {
            color: $primary-purple;
        }
        &::after {
            position: absolute;
            content: "";
            background: url("../img/arrow_right.svg");
            background-repeat: no-repeat;
            background-position: center center;
            width: 24px;
            height: 24px;
            display: inline-block;
            top: -3px;
            right: 0;
        }
    }
    &__list {
        display: none;
        position: relative;
        z-index: 2;
        margin: 0;
        list-style: none;
        background: $white;
        box-shadow: 0px 26px 26px $black-transparency;
    }
    &__item {
        padding: 10px;
        & a {
            color: $grey-2;
            &:hover {
                color: $primary-purple;
            }
        }

    }
}

@media (max-width: 767px) {
    .mob-menu {
        display: block;
    }
}