.reviews {
	position: relative;
	display: flex;
	background: $grey-5;
	border-radius: 16px;
	padding: 79px 68.5px 79px 68.5px;
    &__list {
        padding: 0 12.5px;
        width: 50%;
    }
    &__personal {
        padding: 16px;
        background: $white;
        box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.1);
        border-radius: 16px;
        display: flex;
        align-items: self-start;
    }
    &__photo {
        margin-right: 30px;
        border-radius: 10px;
        max-width: 100%;
        min-width: 69px;
        height: auto;
    }
    &__text {
        font-size: 18px;
        line-height: 140%;
        color: $grey-2;
        margin: 0;
        width: 65%;
        max-width: 269px;
    }
} 
@media (max-width: 991px) {
    .reviews {
        &__list {
            width: 100%;
            &-none {
                display: none;
            }
        }
        &__text {
            max-width: none;
        }
    }
}
@media (max-width: 767px) {
    .reviews {
        &-top {
            margin-top: 67px;
        }
        margin-top: 3px;
        border-radius: 0;
        padding: 30px 15px 95px 15px;
        &__list {
            padding: 0;
        }
        &__photo {
            border-radius: 4px;
            margin-right: 16px;
            width: 69px;
            height: auto;
        }
        &__text {
            width: 72%;
        }
    }
}